* {
  background: #3C3744;
  color: #B4C5E4;
}

#App {
  display:flex;
  justify-content: center;
  align-items: center;
  height:90vh;
  flex-direction: column;
}

#App .input {
  background:  #B4C5E4;
  color:#3C3744;
  font-size: 20px;
  transition: 0.2s;
}

#App #country {
  background: #B4C5E4;
  color:#3C3744;
  font-size: 20px;
  transition: 0.2s;
}

#App .input:hover, #App #country:hover {
  background: #3C3744;
  color:#B4C5E4;
}

span {
  font-size: 16px;
}

@media (max-width: 768px) {
  #App {
    text-align: center;
  }

  #App .input, #App #country {
    max-width: 80vw; 
  }
}

button {
  width: 100px;
  height: 25px;
  border-radius: 5px;
}